import React, { Component } from 'react';
import '../App.css';
import Lightbox from 'react-images';
import { images } from '../constants/images';

export default class Clues extends Component {
  constructor(props){
    super(props);
    this.state = {
      lightboxIsOpen: false,
      currentImage: 0
    }
  }

  closeLightbox() {
    this.setState({ lightboxIsOpen: false });
  }

  openLightbox(number) {
    this.setState({ 
      lightboxIsOpen: true,
      currentImage: number - 1
    });
  }

  render() {
    const { clues, correctAnswers } = this.props;
    return (
      <div>
      <ol className="clues">
        { 
          clues.map((clueObject, idx) => {
            if (correctAnswers.some(o => o === clueObject.number)) {
              return (
                <div key={idx}>
                  <li>
                    <span className="clue clue-correct">
                      {clueObject.clue}
                    </span>
                    <span className="clue-correct__text">
                      Верно!
                    </span>
                  </li>               
                  <div>      
                    <Lightbox 
                      images={ images }
                      currentImage={this.state.currentImage}
                      isOpen={this.state.lightboxIsOpen}
                      onClose={() => this.closeLightbox()}
                      showImageCount={false}                      
                      backdropClosesModal={true}
                    />
                  </div>     
                </div>
              )
            }
            return (
              <div key={idx}>
                <li className="clue" key={idx}>
                  <span className="clue">
                      {clueObject.clue}
                  </span>
                  <input
                    className="hint-button"
                    type="button"
                    value="Подсказка"
                    onClick={() => this.openLightbox(clueObject.number)}
                  >
                  </input>
                </li>
                <div>      
                  <Lightbox 
                    images={ images }
                    currentImage={this.state.currentImage}
                    isOpen={this.state.lightboxIsOpen}
                    onClose={() => this.closeLightbox()}
                    showImageCount={false}
                    backdropClosesModal={true}
                  />
                </div>
              </div>
            )
          })
        }
      </ol>
</div>
    )
  }
}


