import React, { Component } from 'react';
import '../App.css';
import $ from "jquery";

export default class Box extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: null,
      isCorrect: false
    };
    
    this.handleChange = this.handleChange.bind(this);
  }
  
  handleChange(event) {
    const { clueAcross, letter } = this.props;
    const { isCorrect, value } = this.state;
    const inputValue = event.target.value;

    const cluesToUpdate = [{
      direction: "across",
      number: clueAcross
    }];
    
    if (inputValue.toUpperCase() === letter && !isCorrect) {
      this.setState({
        value: inputValue,
        isCorrect: true
      });

      this.props.onCorrect(cluesToUpdate);
    }
    else if (isCorrect && value && inputValue.toUpperCase() !== letter) {
      this.setState({
        value: inputValue,
        isCorrect: false
      });
      
      this.props.onIncorrect(cluesToUpdate);
    }
  }

  checkBoxIsAnswer = (cell) => cell.match(/\w[3]/i)

  onKeyUpHandler(e) {
    let input = e.currentTarget;
    var maxLength = ~~input.getAttribute('maxlength');
    var key = e.keyCode || e.charCode;    
    debugger
    if( key === 8 || key === 46 ) {
        if (input.value) return;
          
        const parent = input.parentElement;
        const prevParent = parent.previousSibling;
        if (prevParent) {
          input = $(prevParent).children('input.box')[0];
        }

        if (input) {
          input.value = "";
        }        
    } else if (maxLength && input.value.length >= maxLength){
        do {
            const parent = input.parentElement;
            const nextParent = parent.nextSibling;
            if (nextParent) {
              input = $(nextParent).children('input.box')[0];
            }
        }
        while(input && !$(input).is('input.box'));
    }

    if(input && $(input).is('input.box')){
        input.focus();
    }
  }
  
  render() {
    const { letter, number, id } = this.props;
    if (!letter) {
      return (
        <div className="box blank"></div>
       )
    }
    return (
      <div className="box-wrapper">
        { number 
          && <label className="box-label box-label--number">
                {this.props.number}
              </label> 
        }
        <input
          type="text" 
          onChange={this.handleChange} 
          maxLength="1"
          onKeyUp={this.onKeyUpHandler}
          className={ 
            "box " +
            `${this.checkBoxIsAnswer(id) ? 'box-answer' : ''} ` +
            `${this.props.isFinished ? 'finished' : ''}`
          }
          disabled={!!this.props.disabled} 
        />
      </div>
    )
  }
}


