import React, { Component } from 'react';
import './App.css';
import Clues from './containers/Clues';
import Boxes from './containers/Boxes';
import { grid } from './constants/grid'
import { clues } from './constants/clues'

export default class Crossword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clues: clues,
      grid: grid,
      correctAnswers: [],
      isFinished: false
    }

    this.onCorrectAnswerHandler.bind(this);
  }

  onCorrectAnswerHandler(number) {
    const answers = this.state.correctAnswers;
    answers.push(number);
    this.setState({
      correctAnswers: answers
    })
  }

  onFinishedHandler() {
    this.setState({
      isFinished: true
    })
  }

  renderDownloader() {
    return (
      <div className="link-container">
        <p className="link-text">Получить подарок!</p>
        <a 
          className="link-image"
          href={ require('./media/doc.tif') }
          download="Подарочек"
        >
          <img alt="Нажми меня, я не вирус :)" width="100px" height="100px" src={ require('./media/download.png') }/>
        </a>
      </div>
    )
  }

  render() {
    return (
      <div className="crossword">
        <div className="clue-lists">
          <div>
            <Clues clues={this.state.clues.across} correctAnswers={this.state.correctAnswers} />
          </div>
        </div>
        <br/>
        <Boxes 
          clues={this.state.clues} 
          onCorrectAnswer={(number) => this.onCorrectAnswerHandler(number)}
          onFinishedHandler={() => this.onFinishedHandler()}
          grid={grid} 
        />
        { this.state.isFinished && this.renderDownloader() }        
      </div>
    )
  }
}


