import React, { Component } from 'react';
import Box from './Box';
import '../App.css';

export default class Boxes extends Component {
   constructor(props) {
     super(props);
     this.state = {
      answers: {
        across: props.clues.across.map((clue) => { 
          return this.initClueData(clue);
        })
      },
      correctLetters: 0,
      disabledRows: [],
      isFinished: false
     };
     
     this.handleCorrectGuess = this.handleCorrectGuess.bind(this);
     this.handleIncorrectGuess = this.handleIncorrectGuess.bind(this);
   }

   initClueData(data) {
     return {
       answer: data.answer,
       correct: 0,
       solved: false
     }
   }

   handleCorrectGuess(cluesToUpdate) {
     let { disabledRows, isFinished } = this.state;

     console.log('cluesToUpdate');
     console.log(cluesToUpdate);
     console.log('state');
     console.log(this.state);


     for (const clue of cluesToUpdate) {
       const clueIndex = clue.number - 1;
       let currentState = this.state.answers;
       let currentClueObject = currentState[clue.direction][clueIndex];
       currentClueObject.correct++;
       
       if (currentClueObject.correct === currentClueObject.answer.length) {
         currentClueObject.solved = true;
         disabledRows.push(clue.number);
         this.props.onCorrectAnswer(clue.number);
       }
       
       currentState[clue.direction][clueIndex] = currentClueObject;
       
       console.log('disable condition');
       console.log(currentState.across.length);
       console.log(disabledRows.length);
       if (currentState.across.length === disabledRows.length) {          
          this.props.onFinishedHandler();
          isFinished = true;
       }

       this.setState({
         answers: currentState,
         disabledRows,
         isFinished
       });
       
     }
   }

   handleIncorrectGuess(cluesToUpdate) {
      let currentState = this.state.answers;
      for (const clue of cluesToUpdate) {
        const clueIndex = clue.number - 1;
        let currentClueObject = currentState[clue.direction][clueIndex];
        currentClueObject.correct--;

        currentState[clue.direction][clueIndex] = currentClueObject;


        this.setState({
          answers: currentState
        });
      }
    }

   render() {
     return (
      <div className="boxes">
         { this.props.grid.map(box => 
            <Box 
              key={box.id}
              letter={box.letter}
              across={box.across}
              id={box.id}
              clueAcross={box.clue_across}
              number={box.number}
              answers={this.state.answers}
              onCorrect={this.handleCorrectGuess}
              onIncorrect={this.handleIncorrectGuess}
              disabled={ this.state.disabledRows.some(o => box.clue_across === o) }
              isFinished={ this.state.isFinished }
            />
          )}
       </div>
     )
   }
 }


